.header{
    background: var(--background);
    border-bottom: 1px solid var(--border);
    position: fixed;
    top: 0;
    width: 100%;
}

.header .container{
    display: flex;
    align-items: center;
    justify-content: space-between; /* dá expassamento*/
    height: 60px;
    max-width: 965px; /*para que serve?*/
    margin: 0 auto; /* margem em cima e em aixo 0 e dos lados é automatico*/
    
}

.header .container .logo{
    width: 120px;
    cursor: pointer;
    width: 103px;

}

.input-fake{
   background: var(--background-main);
   border-radius: 5px;
   border: 1px solid var(--border);
   display: flex;
   align-items: center;
   padding: 5px 10px;
}

.input-fake input{
    font-size: 14px;
    padding: 0px 7px;


}

.menu-icons{
    display: flex;
    align-items: center;
    gap: 16px;
    cursor: pointer;
}

.menu-icons .img-user{
width: 26px;
height: 26px;
border-radius: 50%;

}