* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
  Arial, sans-serif;
  border: 0; /* tira as bordas do botão e as bordas do input*/
  outline: 0; /*para quando clicar no botão ou imput não fica com a borda preta */
  background: none;
/* para não vir aquele background padrão ex: nos botoes alguns vem com o uma cor de fundo */

}


html, body #root {
  max-width: 100vw; /*view port width*/
  max-height: 100vh; /*view port height*/
  width: 100%;
  height: 100%;
  /* todo o espaço da tela é ocupada*/
}


html{
  background: var(--background-main);

}

p, span{
 color: var(--primary)
}

:root {
  --background-main: #f6f6f6 ;
  --background: #fff;
  --primary: #262626;
  --secondary: #8e8e8e;
  --tertiary:#c7c7c7;
  --quartary: #0095f6;
  --border:#dbdbdb;
  --border-secondary:#efefef;
  /*variavel dentro do css (coloquei o nome da variavel e o valor que ela vai receber*/
}






















































