.containerum{
    padding: 15px ;
    display: flex;
    gap: 16px;
    background: var(--background);
    border-radius: 10px;
    border: 1px solid var(--border);
}

.story-elements{
    cursor:pointer
}

.user-elements div{
    width: 60px;
    height: 60px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient( #c82d8e, #f99d4c);
}

.image-user-story{
     width: 56px;
     height: 56px;
     border-radius: 50%;
     border: 2px solid var(--background);
     right: 60px;
}

.user-elements span{
    font-size: 13px;
    cursor: pointer;
    
    
